import React, { useState, useEffect } from "react";
import ReactPlayer from "react-player";
import ShowMoreText from "react-show-more-text";
import { useParams } from "react-router-dom";
import axios from "axios";
import MovieRecommendations from "../RecommendedMovies/recommendedMovies";
import "./movieDetails.css";

export default function MovieDetails() {
  const { id } = useParams();
  const [movieVideo, setMovieVideo] = useState([]);
  const [movieDetails, setMovieDetails] = useState([]);
  const [movieGenres, setMovieGenres] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/movie/${id}/videos?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
      )
      .then((res) => {
        if (
          typeof res.data.results !== "undefined" &&
          res.data.results.length > 0
        ) {
          setMovieVideo(res.data.results[0].key);
        } else {
          setMovieVideo();
        }
      });
    axios
      .get(
        `https://api.themoviedb.org/3/movie/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
      )
      .then((res) => {
        setMovieDetails(res.data);
        setMovieGenres(res.data.genres);
      });
  }, []);

  let time = movieDetails.runtime;
  var Hours = Math.floor(time / 60);
  var minutes = time % 60;

  return (
    <div>
      <div className="movieDetails_body">
        <div className="youtube-player">
          <div className="player-wrapper">
            <ReactPlayer
              className="react-player"
              url={`https://www.youtube.com/watch?v=${movieVideo}`}
              controls={true}
              width="100%"
              height="100%"
            />
          </div>
        </div>
        <div className="movie_details">
          <div className="title"> {movieDetails.title} </div>
          <div className="details">
            <ul className="list-inline">
              <li className="list-inline-item">
                {time > 0 && (
                  <span className="details_items">
                    {" "}
                    {Hours} hrs {minutes} min,{" "}
                  </span>
                )}
              </li>
              <li className="list-inline-item">
                {movieGenres.map((data) => {
                  return (
                    <li key={data.id} className="list-inline-item">
                      <span className="details_items">{data.name},</span>
                    </li>
                  );
                })}
              </li>
              <li className="list-inline-item">
                {movieDetails.original_language === "en" && (
                  <span className="details_items"> English </span>
                )}
                {movieDetails.original_language === "ja" && (
                  <span className="details_items"> Japanese </span>
                )}
                {movieDetails.original_language === "ko" && (
                  <span className="details_items"> Korean </span>
                )}
                {movieDetails.original_language === "hi" && (
                  <span className="details_items"> Hindi </span>
                )}
              </li>
              <li></li>
            </ul>
          </div>
          <div className="tagline">{movieDetails.tagline}</div>

          <h4 style={{ color: "white" }}>Overview:</h4>
          <div className="overview">
            <ShowMoreText>
              <p>{movieDetails.overview}</p>
            </ShowMoreText>
          </div>
        </div>
      </div>

      <div style={{ backgroundColor: "black" }}>
        <MovieRecommendations />
      </div>
    </div>
  );
}
