import React, { useState, useEffect } from "react";
import {
  makeStyles,
  Grid,
  Card,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory, useParams } from "react-router-dom";
import Carousel from "react-elastic-carousel";
import axios from "axios";

const userStyles = makeStyles(() => ({
  root: {
    minWidth: 140,
    maxWidth: 140,
    flexGrow: 1,
  },
}));

export default function RecommendedTvShow() {
  const classes = userStyles();
  let { id } = useParams();
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/tv/${id}/recommendations?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&page=1`
      )
      .then((response) => {
        setData(response.data.results);
      });
  }, []);

  const history = useHistory();

  const breakPoints = [
    { width: 1, itemsToShow: 1 },
    { width: 250, itemsToShow: 2 },
    { width: 400, itemsToShow: 3 },
    { width: 550, itemsToShow: 4 },
    { width: 768, itemsToShow: 6 },
    { width: 1200, itemsToShow: 6 },
  ];

  return (
    <div>
      <div className="movie_container">
        <h3 style={{ color: "white" }}>Recommended Shows</h3>

        <Carousel
          breakPoints={breakPoints}
          itemsToScroll={1}
          pagination={false}
          showArrows={true}
        >
          {data.map((data, index) => (
            <Grid
              container
              direction="row"
              justify="center"
              alignItems="center"
              spacing={3}
              xs={12}
              sm={6}
              key={index}
            >
              {" "}
              <Card
                className={classes.root}
                onClick={() => {
                  history.push(`/tv/${data.name}/${data.id}`);
                  window.location.reload(true);
                }}
              >
                <CardActionArea>
                  <CardMedia
                    component="img"
                    objectFit="cover"
                    image={"https://image.tmdb.org/t/p/w500" + data.poster_path}
                    title={data.name}
                    id={data.id}
                  />
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Carousel>
      </div>
    </div>
  );
}
