import React, { useState, useEffect } from "react";
import {
  makeStyles,
  Grid,
  Card,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory, useLocation } from "react-router-dom";
import axios from "axios";
import qs from "qs";
import "./movieList.css";

const userStyles = makeStyles(() => ({
  root: {
    minWidth: 140,
  },
}));

export default function MovieList() {
  const classes = userStyles();
  let location = useLocation();
  let name = qs.parse(location.search, { ignoreQueryPrefix: true }).query;
  const [data, setData] = useState([]);
  const [isLoading, setLoading] = useState();

  useEffect(() => {
    async function SearchMovies() {
      setLoading(true);
      const result = await axios.get(
        `https://api.themoviedb.org/3/search/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${name}&page=1&include_adult=false`
      );
      if (result.data != null) {
        setLoading(false)
        setData(result.data.results);
      } else {
        setData(null);
      }
      console.log(result.data);
    }
    SearchMovies();
  }, [name]);

  const history = useHistory();
  return (
    <div>
      {isLoading ? (
        "Loading..."
      ) : (
        <div className="spaceing">
          <Grid
            container
            spacing={3}
            direction="row"
            justify="flex-start"
            alignItems="center"
            className={classes.root}
          >
            {data.map((data, index) =>
              data.poster_path !== null ? (
                <Grid item xs={12} sm={2} key={index}>
                  <Card
                    onClick={() => {
                      history.push(`/movie/${data.title}/${data.id}`);
                    }}
                  >
                    <CardActionArea>
                      <CardMedia
                        component="img"
                        objectFit="cover"
                        image={
                          "https://image.tmdb.org/t/p/w500" + data.poster_path
                        }
                        title={data.title}
                      />
                    </CardActionArea>
                  </Card>
                </Grid>
              ) : null
            )}
          </Grid>
        </div>
      )}
    </div>
  );
}
